.number{
  position: absolute;
  left: 30px;
  bottom: 20px;
  font-size: 12px;
  color: #000;
  &.right{
    left: auto;
    right: 30px;
  }
}
.header{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  justify-content: space-between;
  z-index: 1000;
  padding-top: 20px;
  img{
    width: 160px;
  }
  a{
    font-size: 12px;
    letter-spacing: 1.2px;
  }
  a.white{
    color: #fff;
  }
}

.swiper-slide{
  height: 100vh;
}


.slide-1{
  background: url(../img/OH_MY_STILL_LIFE97929_fin_hor.jpg) no-repeat center bottom / cover;
  display: flex;
  align-items: center;
  justify-content: center;
  .flex{
    transform: translateX(60px);
  }
}

.logo{
  width: 200px;
  img{
    width: 100%;
  }
}

.logo-absolute{
  position: absolute;
  left: 50%;
  bottom: 28px;
  transform: translateX(-50%);
}

.flex{
  display: flex;
  justify-content: center;
  align-items: center;
}

.text{
  font-size: 35px;
  letter-spacing: 1px;
  color: #fff;
  text-transform: uppercase;
  margin-left: 80px;
}

.flex-left{
  width: 50%;
  height: 100vh;
}

.flex-right{
  width: 50%;
  overflow: hidden;
}

.description{
  font-size: 12px;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}

.title{
  font-weight: 500;
  font-size: 4vw;
  line-height: 4vw;
  b{
    font-weight: bold;
  }
}

.line{
  display: inline-block;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    left: -5px;
    height: 3px;
    width: calc(100% + 10px);
    background-color: #ceabd1;
    top: 2.3vw;
  }
}


.slide-2{
  .description{
    text-align: right;
    margin-bottom: 30px;
    padding-right: 35px;
  }

  .pic{
    transform: translateX(10vw);
    img{
      width: auto;
    }
  }

  .title{
    padding-left: 100px;
  }
}



.slide-3{
  .flex-left{
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    align-items: flex-end;
  }
  .flex-right{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .title{
    padding-left: 100px;
    padding-top: 70px;
    margin-bottom: 30px;
    width: 100%;
    height: 150px;
  }
  .pic{
    display: flex;
    align-items: flex-end;
    font-size: 4vw;
    justify-content: center;
    font-weight: 500;
    padding-bottom: 50px;
    letter-spacing: 4px;
  }
  .pic-1{
    height: 60vh;
    width: 500px;
    margin-right: 120px;
    margin-bottom: 10vh;
    padding-bottom: 3vw;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      width: 20px;
      height: 100%;
      left: 100%;
      top: 0;
      margin-left: 50px;
      background-color: #fbf3a7;
    }
  }
  .pic-2{
    height: 75vh;
    width: calc(50% - 120px);
    position: relative;
    margin-bottom: 2vh;
    padding-bottom: 5.8vw;
    &:after{
      content: "";
      position: absolute;
      width: 20px;
      height: 100%;
      left: 100%;
      top: 0;
      margin-left: 50px;
      background-color: #ceabd1;
    }
  }
  .pic-3{
    width: 50%;
    height: 90vh;
    margin-top: 2vh;
    padding-bottom: 7vw;
  }
}

a{
  display: block;
}
