      width: 100%;
    }
  }
  .pic-3{
    position: absolute;
    top: 0;
    right: 0;
    width: 40vw;
  }
  .flex-right{
    width: 50%;
    height: 100vh;
    position: relative;
    
  }

  .descriptions{
    position: absolute;
    right: 50px;
    bottom: 100px;
    
  }

  .description{
    &:first-child{
